<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table
        :data="rolList"
        style="width: 100%"
        border
        stripe
      >
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="(item1,i1) in scope.row.children"
              :key="item1.id"
              :class="['bdbottom',i1==0?'bdtop':'','vcenter']"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag closable>
                  {{item1.authName}}
                </el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row
                  v-for="(item2,i2) in item1.children"
                  :key="item2.id"
                  :class="[i2==0?'':'bdtop','vcenter']"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                    >
                      {{item2.authName}}
                    </el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="(item3) in item2.children"
                      :key="item3.id"
                      closable
                      @close='removeRightsById(scope.row,item3.id)'
                    >
                      {{item3.authName}}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <!--  -->
            <!-- <pre>{{ scope.row }}</pre> -->
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index">
        </el-table-column>
        <el-table-column
          prop="roleName"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="roleDesc"
          label=""
        >
        </el-table-column>
        <el-table-column
          label=""
          width="300px"
        >
          <template slot-scope="scope">
            <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit"
            ></el-button>
            <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
            ></el-button>
            <el-button
              type="warning"
              size="mini"
              icon="el-icon-setting"
              @click="showSetRightDialog(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="showSetRightDialogVisable"
      width="50%"
      @close="setRightDialogClose"
    >
      <el-tree
        :data="rightsList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        :default-expand-all="true"
        :default-checked-keys="defkeys"
        ref="treeRef"
      ></el-tree>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="showSetRightDialogVisable = false"> </el-button>
        <el-button
          type="primary"
          @click="allotRights"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      rolList: [],
      // 
      showSetRightDialogVisable: false,
      // 
      rightsList: [],
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // 
      defkeys: [],
      // id
      roleId: ''
    }
  },
  created() {
    this.getRolList()
  },
  methods: {
    async getRolList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('')
      this.rolList = res.data
    },
    // id
    async removeRightsById(role, rightID) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightID}`)
        if (res.meta.status !== 200) {
          this.$message.error('')
          return
        }
        // 
        role.children = res.data
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    async showSetRightDialog(role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error('')
      // 
      this.rightsList = res.data
      //  nodearr
      this.getLeafKeys(role, this.defkeys)
      this.showSetRightDialogVisable = true
    },
    //  nodearr
    getLeafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // 
    setRightDialogClose() {
      this.defkeys = []
    },
    // 
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedNodes()
      ]
      //   keys.pop()
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      // 
      this.getRolList()
      this.showSetRightDialogVisable = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}

.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
