<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getUserList()"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList()"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button
            type="primary"
            @click="addDialogVisible = true"
          ></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table
        :data="userList"
        style="width: 100%"
        border
        stripe
      >
        <el-table-column type="index">
        </el-table-column>
        <el-table-column
          prop="username"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="email"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="mobile"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="role_name"
          label=""
        >
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStateChange(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip
              effect="dark"
              content=""
              :enterable="false"
              placement="top"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialoge(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="danger"
                size="mini"
                icon="el-icon-delete"
                @click="removeUserById(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip
              effect="dark"
              content=""
              :enterable="false"
              placement="top"
            >
              <el-button
                type="warning"
                size="mini"
                icon="el-icon-setting"
                @click="setRole(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClose"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="70px"
      >
        <el-form-item
          label=""
          prop="username"
        >
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="password"
        >
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="email"
        >
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="mobile"
        >
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="addUser"
        > </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClose"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="70px"
      >
        <el-form-item label="">
          <el-input
            v-model="editForm.username"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="email"
        >
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="mobile"
        >
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="editUserInfo"
        > </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRoleDialogVisible"
      width="50%"
    >
      <div>
        <p>{{userInfo.username}}</p>
        <p>{{userInfo.role_name}}</p>
        <p>
          <el-select
            v-model="selectdRoleId"
            placeholder=""
            @close="setRoleDialogClose"
          >
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="setRoleDialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="saveRoleInfo"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkEmail = (rule, value, callback) => {
      const regEmail = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/
      if (regEmail.test(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    // 
    var checkMobile = (rule, value, callback) => {
      const regMobile = /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-7|9])|(?:5[0-3|5-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[1|8|9]))\d{8}$/
      if (regMobile.test(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      // 
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        pagesize: 2
      },
      userList: [],
      total: 0,
      // 
      addDialogVisible: false,
      // 
      editDialogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      editForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      addFormRules: {
        username: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          min: 3,
          max: 10,
          message: '3~10',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          min: 6,
          max: 15,
          message: '6~15',
          trigger: 'blur'
        }],
        email: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
        { validator: checkEmail, trigger: 'blur' }],
        mobile: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
        { validator: checkMobile, trigger: 'blur' }]
      },
      // 
      editFormRules: {
        email: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
        { validator: checkEmail, trigger: 'blur' }],
        mobile: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
        { validator: checkMobile, trigger: 'blur' }]
      },
      // 
      setRoleDialogVisible: false,
      // 
      userInfo: {},
      // 
      rolesList: [],
      // 
      selectdRoleId: ''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get('users', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.userList = res.data.users
      this.total = res.data.total
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // switch
    async userStateChange(userInfo) {
      const { data: res } = await this.$http.put(`users/${userInfo.id}/state/${userInfo.mg_state}`)
      if (res.meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          // 
          const { data: res } = await this.$http.post('users', this.addForm)
          if (res.status !== 201) {
            this.$message.error('')
          }
          this.$message.success('')
          this.addDialogVisible = false
          this.getUserList()
        }
      })
    },
    // 
    async showEditDialoge(id) {
      this.editDialogVisible = true
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editForm = res.data
    },
    // 
    editDialogClose() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editUserInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: res } = await this.$http.put('users/' + this.editForm.id, {
          email: this.editForm.email,
          mobile: this.editForm.mobile
        })
        if (res.meta.status !== 200) {
          this.$message.error('')
        }
        this.$message.success('')
        this.getUserList()
        this.editDialogVisible = false
        this.$refs.editFormRef.resetFields()
      })
    },
    // 
    removeUserById(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete('users/' + id)
        if (res.meta.status !== 200) {
          this.$message.error('')
          return
        }
        this.getUserList()
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    async setRole(userInfo) {
      // 
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('')
      // id
      this.rolesList = res.data
      // 
      this.userInfo = userInfo
      this.setRoleDialogVisible = true
    },
    async saveRoleInfo() {
      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, { rid: this.selectdRoleId })
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getUserList()
      this.setRoleDialogVisible = false
    },
    setRoleDialogClose() {
      this.userInfo = {}
      this.selectdRoleId = ''
    }
  }
}
</script>
